<template>
  <div>
    <h1>Vendors Sales Comparison 2016 vs 2017</h1>
    <h2>(Not yet working)</h2>

    <div>
      Search <input type="text" v-model="query" @keyup="selectRows">
    </div>
    <table class="table table-striped">
      <thead>
        <tr>
          <th style="text-align : left">Name</th>
          <th style="text-align : right">2016</th>
          <th style="text-align : right">2017</th>
          <th style="text-align : right">Difference</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="vendor in selectedData">
          <td align="left">{{vendor.name}}</td>
          <td align="right">{{vendor.sales2016}}</td>
          <td align="right">{{vendor.sales2017}}</td>
          <td align="right"><span v-if="vendor.difference > 0">+</span>{{vendor.difference}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'vendors',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      query: "",
      selectedData : [],
      allData : []
    }
  },
  created() {

    var vm = this;

    $.getJSON('/static/scripts/totalSalesByVendor2016.json')
      .done( function(data2016) {
          $.getJSON('/static/scripts/totalSalesByVendor2017.json')
              .done( function(data2017) {
                $.getJSON('/static/scripts/vendors.json')
                    .done( function(vendorList) {
                      // Build a map of all vendor numbers
                      var vendors = {};

                      for( var i = 0; i < vendorList.length; i++) {
                          vendors[ vendorList[i].vendorNumber ] = {
                            name : vendorList[i].vendorName,
                            sales2016: 0,
                            sales2017: 0,
                            difference: 0
                          };
                      }

                      for( var i= 0; i < data2016.length; i++ ) {
                        var record = data2016[i];
                        if( record.vendorNumber in vendors ) {
                          vendors[ record.vendorNumber ].sales2016 = parseInt(record.totalSales);
                        }
                      }

                      for( var i= 0; i < data2017.length; i++ ) {
                        var record = data2017[i];
                        if( record.vendorNumber in vendors ) {
                          vendors[ record.vendorNumber ].sales2017 = parseInt(record.totalSales);
                        }
                      }

                      vm.allData.splice( 0, vm.allData.length );

                      for( var key in vendors ) {
                        var record = vendors[key];
                        record.difference = record.sales2017 - record.sales2016;

                        vm.allData.push(record);
                      }
                      console.log('OK');

                      vm.selectRows();
                });
          });
      });


  },
  methods : {
    loadVendor(vendid) {
        this.$router.push( '/vendors/' + vendid)
    },
    selectRows() {
      var query = this.query.toLowerCase();

      this.selectedData.splice( 0, this.selectedData.length );

      for(var i=0; i < this.allData.length; i++) {
        var name = this.allData[i].name.toLowerCase();
        if ( query === "" || name.includes(query)) {
          this.selectedData.push(this.allData[i]);
        }
      }
    }
  }
}
</script>

