<template>
  <div>
    <h1>Items Sales Comparison 2016 vs 2017</h1>

    <div>
      Search <input type="number" v-model="query" @keyup="selectRows">
    </div>
    <table class="table table-striped">
      <thead>
        <tr>
          <th style="text-align : left">Item Number</th>
          <th style="text-align : right">2016</th>
          <th style="text-align : right">2017</th>
          <th style="text-align : right">Difference</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in selectedData">
          <td align="left">{{item.name}}</td>
          <td align="right">{{item.sales2016}}</td>
          <td align="right">{{item.sales2017}}</td>
          <td align="right"><span v-if="item.difference > 0">+</span>{{item.difference}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'items',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      query: "",
      selectedData : [],
      allData : [
        {
          name : "Beta Cafe",
          sales2016 : 100,
          sales2017 : 200,
          difference : 100
        },
        {
          name : "Alamo Cafe",
          sales2016 : 100,
          sales2017 : 200,
          difference : 100
        },
        {
          name : "Alamo 2 Cafe",
          sales2016 : 140,
          sales2017 : 250,
          difference : -1560
        }

      ]
    }
  },
  created() {

    var vm = this;

    $.getJSON('/static/scripts/totalSalesByItem2016.json')
      .done( function(data2016) {
          $.getJSON('/static/scripts/totalSalesByItem2017.json')
              .done( function(data2017) {
                    // Build a map of all item numbers

                    var items = {};

                    for( var i= 0; i < data2016.length; i++ ) {
                      var record = data2016[i];
                      items[ record.itemNumber ] = {
                          name : record.itemNumber,
                          sales2016 : parseInt(record.totalSales),
                          sales2017 : 0,
                          difference: 0
                        }

                    }
                    console.log(items);

                    for( var i= 0; i < data2017.length; i++ ) {
                      var record = data2017[i];
                      if( record.itemNumber in items ) {
                        items[ record.itemNumber ].sales2017 = parseInt(record.totalSales);
                      }
                    }

                    vm.allData.splice( 0, vm.allData.length );

                    for( var key in items ) {
                      var record = items[key];
                      record.difference = record.sales2017 - record.sales2016;

                      vm.allData.push(record);
                    }
                    console.log('OK');

                    vm.selectRows();
              });
          });


  },
  methods : {
    loadItem(vendid) {
        this.$router.push( '/items/' + vendid)
    },
    selectRows() {
      var query = this.query;

      this.selectedData.splice( 0, this.selectedData.length );

      for(var i=0; i < this.allData.length; i++) {
        var name = this.allData[i].name;
        if ( query === "" || name == query) {
          this.selectedData.push(this.allData[i]);
        }
      }
    }
  }
}
</script>

