<template>
  <div>
    <h1>Customers Sales Comparison 2016 vs 2017</h1>

    <div>
      Search <input type="text" v-model="query" @keyup="selectRows">
    </div>
    <table class="table table-striped">
      <thead>
        <tr>
          <th style="text-align : left">Name</th>
          <th style="text-align : right">2016</th>
          <th style="text-align : right">2017</th>
          <th style="text-align : right">Difference</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="customer in selectedData">
          <td align="left">{{customer.name}}</td>
          <td align="right">{{customer.sales2016}}</td>
          <td align="right">{{customer.sales2017}}</td>
          <td align="right"><span v-if="customer.difference > 0">+</span>{{customer.difference}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'customers',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      query: "",
      selectedData : [],
      allData : [
        {
          name : "Beta Cafe",
          sales2016 : 100,
          sales2017 : 200,
          difference : 100
        },
        {
          name : "Alamo Cafe",
          sales2016 : 100,
          sales2017 : 200,
          difference : 100
        },
        {
          name : "Alamo 2 Cafe",
          sales2016 : 140,
          sales2017 : 250,
          difference : -1560
        }

      ]
    }
  },
  created() {

    var vm = this;

    $.getJSON('/static/scripts/totalSalesByCustomer2016.json')
      .done( function(data2016) {
          $.getJSON('/static/scripts/totalSalesByCustomer2017.json')
              .done( function(data2017) {
                $.getJSON('/static/scripts/customers.json')
                    .done( function(customerList) {
                      // Build a map of all customer numbers
                      var customers = {};

                      for( var i = 0; i < customerList.length; i++) {
                          customers[ customerList[i].customerNumber ] = {
                            name : customerList[i].customerName,
                            sales2016: 0,
                            sales2017: 0,
                            difference: 0
                          };
                      }

                      for( var i= 0; i < data2016.length; i++ ) {
                        var record = data2016[i];
                        if( record.customerNumber in customers ) {
                          customers[ record.customerNumber ].sales2016 = parseInt(record.totalSales);
                        }
                      }

                      for( var i= 0; i < data2017.length; i++ ) {
                        var record = data2017[i];
                        if( record.customerNumber in customers ) {
                          customers[ record.customerNumber ].sales2017 = parseInt(record.totalSales);
                        }
                      }

                      vm.allData.splice( 0, vm.allData.length );

                      for( var key in customers ) {
                        var record = customers[key];
                        record.difference = record.sales2017 - record.sales2016;

                        vm.allData.push(record);
                      }
                      console.log('OK');

                      vm.selectRows();
                });
          });
      });


  },
  methods : {
    loadCustomer(vendid) {
        this.$router.push( '/customers/' + vendid)
    },
    selectRows() {
      var query = this.query.toLowerCase();

      this.selectedData.splice( 0, this.selectedData.length );

      for(var i=0; i < this.allData.length; i++) {
        var name = this.allData[i].name.toLowerCase();
        if ( query === "" || name.includes(query)) {
          this.selectedData.push(this.allData[i]);
        }
      }
    }
  }
}
</script>

